<template>
  <div class="index">
    <div class="top">
      <img :src="img" alt="">
    </div>
    <mu-refresh-control :refreshing="refreshing" :trigger="trigger" @refresh="refresh"/>
    <ul ref="newsList">
      <li v-for="(item, index) in list" :key="item.id">
        <p>{{item.text}}</p>
        <p class="blue">
          <span class="left">{{item.time}}</span>
          <span class="right"><i class="good" :class="{'good-c': item.flag=='good'}" @click="upvote(index, 'good')">{{item.favour}}</i><i class="bad" :class="{'bad-c': item.flag=='bad'}" @click="upvote(index, 'bad')">{{item.disfavor}}</i></span>
        </p>
      </li>
    </ul>
    <mu-infinite-scroll :scroller="scroller" :loading="loading" loadingText="正在加载..." @load="loadMore"/>
  </div>
</template>
<script>
export default {
  name: 'index',
  data () {
    return {
      img: './static/images/banner.png',
      page: 1,
      maxPage: '',
      list: [],
      loading: false,
      scroller: null,
      refreshing: false,
      trigger: null
    }
  },
  created () {
    this.getNews()
  },
  mounted () {
    this.scroller = this.$el;
    this.trigger = this.$el;
    let h = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
    this.$el.style.height = h + 'px';
  },
  methods: {
    getNews () {
      this.$http.get('/info/list?page='+this.page).then((data) => {
        if(data.data.code == 'OK') {
          this.list = data.data.data.list
          for (const i in this.list) {
            this.list[i].flag = ''
          }
          this.maxPage = data.data.data.page
        }else {
          alert(data.data.error)
        }
      }).catch((error) => {
        alert('网络繁忙，请稍后再试！')
        console.log(error)
      })
    },
    loadMore () {
      this.loading = true
      if(this.page > this.maxPage) {
        alert("没有更多资讯啦...")
      }else {
        this.page += 1
        this.$http.get('/info/list?page='+this.page).then((data) => {
          this.loading = false
          if(data.data.code == 'OK') {
            this.list.push.apply(this.list, data.data.data.list)
            for (const i in this.list) {
              this.list[i].flag = ''
            }
            this.maxPage = data.data.data.page
          }else {
            alert(data.data.error)
          }
        }).catch((error) => {
          alert('网络繁忙，请稍后再试！')
          console.log(error)
        })
      }
    },
    refresh () {
      this.refreshing = true
      setTimeout(() => {
        this.page = 1
        this.getNews()
        this.refreshing = false
      }, 500)
    },
    upvote (index, flag) {
      if(this.list[index].flag == '') {
        if(flag == 'good') {
          this.$http.get('/info/touch?id='+this.list[index].id+'&type=favour').then((data) => {
            if(data.data.code == 'OK') {
              this.list[index].favour += 1
            }else {
              alert('网络繁忙，请稍后再试！')
            }
          }).catch((error) => {
            alert('网络繁忙，请稍后再试！')
            console.log(error)
          })
        }else {
          this.$http.get('/info/touch?id='+this.list[index].id+'&type=disfavor').then((data) => {
            if(data.data.code == 'OK') {
              this.list[index].disfavor += 1
            }else {
              alert('网络繁忙，请稍后再试！')
            }
          }).catch((error) => {
            alert('网络繁忙，请稍后再试！')
            console.log(error)
          })
        }
        this.list[index].flag = flag
      }
    }
  }
}
</script>
<style>
.index {
  overflow-y: auto;
  /* -webkit-overflow-scrolling: touch; */
  position: relative;
  user-select: none;
  font-size: .75rem;
  box-shadow: 0 5px 15px #666;
}
.index .top {
  position: fixed;
  top: 0;
  width: 100%;
  height: auto;
  margin: 0;
  background: linear-gradient(to bottom, #00adef, #beedff );
  text-align: center;
}
.index .top img {
  width: 100%;
  max-width: 750px;
  height: auto;
}
.index ul {
  max-width: 750px;
  margin:5.2rem auto 0;
}
.index li {
  margin-top: .6rem;
  padding: 0 .5rem .4rem;
  box-shadow: 0 2px 5px 0 #eefaff;
}
.index li p {
  margin: .2rem 0 0;
  padding: 0;
  line-height: 1.6em;
}
.index .right {
  float: right;
}
.index .good {
  margin-right: .5rem;
  padding-left: .9rem;
  background: url(../../static/images/good.png) 0 center no-repeat;
  background-size: .7rem auto;
}
.index .good-c {
  background: url(../../static/images/good_c.png) 0 center no-repeat;
  background-size: .7rem auto;
}
.index .bad {
  padding-left: 1rem;
  background: url(../../static/images/bad.png) 0 center no-repeat;
  background-size: .7rem auto;
}
.index .bad-c {
  background: url(../../static/images/bad_c.png) 0 center no-repeat;
  background-size: .7rem auto;
}
@media screen and (min-width: 400px) {
  .index {
    font-size: 16px;
  }
}
@media screen and (min-width: 750px) {
  .index .good, .index .bad {
    display: inline-block;
    min-height: 24px;
  }
}
</style>


